* {
  box-sizing: border-box;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 16px;
}

body {
  background-color: #fff;
}

.container {
  flex-direction: column;
  justify-content: flex-start;
  gap: 16px;
  width: 640px;
  margin: 16px;
  display: flex;
}

h1 {
  font-size: 1.2rem;
}

.name-wrapper {
  border: 1px solid #777;
  border-radius: 4px;
  width: 100%;
  padding: 8px;
  display: flex;
}

.name-prefix {
  color: #777;
  position: relative;
}

.name-input {
  color: #222;
  background-color: #0000;
  width: 100%;
  margin-left: 4px;
  position: relative;
}

.url-wrapper {
  align-items: center;
  width: 100%;
  display: flex;
}

.url-input {
  border: 1px solid #777;
  border-radius: 4px;
  width: 100%;
  margin-left: 8px;
  padding: 8px;
}

hr {
  border-top: 1px solid #777;
}

